  import urls from '../../../utils/urls.js'
  export default {
    data() {
      return {
        chaptersList: [],
        story: {},
        currentPage: 1,
				isLogin: this.$store.state.isLogin
      }
    },
    onLoad() {
      var title = this.$store.state.pageTitle
      uni.setNavigationBarTitle({
        title: title
      })
      uni.showLoading({
        mask: true,
        title: '正在加载'
      })
      var bookId = this.$store.state.bookId
			
			let id = ''
			if(this.isLogin){
				id = JSON.parse(uni.getStorageSync('userInfo')).id
			}else{
				 id = 0
			}

      var pagenum = this.currentPage
      uni.request({
        url: urls.HTTP + '/novel/getchapterlist',
        method: 'POST',
        data: {
          id: bookId,
          uid: id,
          pagesize: 200,
          pagenum: pagenum
        },
        success: (res) => {
          this.chaptersList = res.data.chapter
          uni.hideLoading()
        },
				fail: () => {
				}
      })
    },
    onReachBottom () {
      uni.showLoading({
        mask: true,
        title: '正在加载'
      })
      var bookId = this.$store.state.bookId
      var uid = uni.getStorageSync('user').id
      this.currentPage++
      var pagenum = this.currentPage
      uni.request({
        url: urls.HTTP + '/novel/getchapterlist',
        method: 'POST',
        data: {
          id: bookId,
          uid: uid,
          pagesize: 200,
          pagenum: pagenum
        },
        success: (res) => {
          console.log(res);
          this.chaptersList = res.data.chapter
          uni.hideLoading()
          if (res.data.islast === 1) {
            uni.showToast({
              title: '已经到底啦',
              icon: 'none'
            })
          }
        }
      })
    },
    methods: {
      isFree(isfree) {
        if (isfree === 1) { // 1为收费
          return false
        } else {
          return true
        }
      },
      goStory(item) {
        var bookId = this.$store.state.story.source_id
        var key = item.id
				let id = '';
				if(this.isLogin){
					id = JSON.parse(uni.getStorageSync('userInfo')).id
				}else{
					 id = 0
				}
       
        uni.request({
          url: urls.HTTP + '/novel/getnovelcontent',
          method: 'POST',
          data: {
            id: bookId,
            key: key,
            uid: id
          },
          success: (res) => {
						
            // console.log(res)
            this.$store.dispatch('storyData', res.data)
            uni.redirectTo({
              url: 'story'
            })
          }
        })
      }
    }
  }
